<template>
  <el-container v-loading="loading" class="page-container">
    <el-aside width="15%">
      <EditorProperties />
    </el-aside>

    <el-container>
      <el-main>
        <Workspace />
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
import EditorProperties from '@/components/EditorProperties.vue';
import Workspace from '@/components/Workspace.vue';

let dragOy;
let dragOx;
let dragging = false;

const successColor = '#67C23A';

const psActions = process.env.ACTIONS_API_URL || 'http://localhost:5000';

const placeDefault = {
  attrs: {
    body: {
      strokeWidth: 2,
      cx: 0,
      cy: 0,
      rx: 40,
      ry: 40,
      stroke: '#8f8f8f',
      fill: '#FFFFFF',
    },
    label: {
      ref: 'body',
      textVerticalAnchor: 'bottom',
      textAnchor: 'middle',
      refX: '50%',
      refY: '0%',
      fontSize: 12,
      fill: '#6d6d6d',
      text: 'New Place',
    },
    dataType: {
      ref: 'body',
      textVerticalAnchor: 'top',
      textAnchor: 'left',
      refX: '90%',
      refY: '90%',
      fontSize: 12,
      fill: '#6d6d6d',
      text: 'integer',
    },
    initialState: {
      ref: 'body',
      textVerticalAnchor: 'top',
      textAnchor: 'left',
      refX: '105%',
      refY: '5%',
      fontSize: 12,
      fill: successColor,
      text: '[]',
    },
    count: {
      ref: 'body',
      textVerticalAnchor: 'middle',
      textAnchor: 'middle',
      refX: '50%',
      refY: '50%',
      fontSize: 12,
      fill: '#6d6d6d',
      text: '0',
    },
  },
};

const placeMarkup = {
  markup: [
    {
      tagName: 'ellipse',
      selector: 'body',
      attributes: {
        magnet: 'on-ctrl',
      },
    },
    {
      tagName: 'text',
      selector: 'label',
    },
    {
      tagName: 'text',
      selector: 'dataType',
    },
    {
      tagName: 'text',
      selector: 'initialState',
    },
    {
      tagName: 'text',
      selector: 'count',
    },
  ],
};

const transitionDefault = {
  ports: {
    groups: {
      input: {
        attrs: {
          circle: {
            fill: '#409EFF',
            stroke: 'none',
            r: 5,
            magnet: 'passive',
            'data-name': '',
            'data-type': '',
          },
          text: {
            fontSize: 12,
            fill: '#6d6d6d',
          },
        },
        label: {
          position: 'insideOriented',
        },
        position: {
          name: 'line',
          args: {
            start: { x: -120, y: -40 },
            end: { x: -120, y: 40 },
          },
        },
      },
      output: {
        attrs: {
          circle: {
            fill: '#67C23A',
            stroke: 'none',
            r: 5,
            magnet: 'on-ctrl',
            'data-name': '',
            'data-type': '',
          },
          text: {
            fontSize: 12,
            fill: '#6d6d6d',
          },
        },
        label: {
          position: 'insideOriented',
        },
        position: {
          name: 'line',
          args: {
            start: { x: 120, y: -40 },
            end: { x: 120, y: 40 },
          },
        },
      },
    },
  },
  attrs: {
    '.': {
      magnet: false,
    },
    body: {
      refx: '100%',
      refy: '100%',
      strokeWidth: 2,
      x: -120,
      y: -40,
      width: 240,
      height: 80,
      rx: 4,
      ry: 4,
      stroke: '#8f8f8f',
      fill: '#FFFFFF',
    },
    label: {
      ref: 'body',
      textVerticalAnchor: 'bottom',
      textAnchor: 'middle',
      refX: '50%',
      refY: '0%',
      fontSize: 12,
      fill: '#6d6d6d',
      text: 'New Transition',
    },
    action: {
      ref: 'body',
      textVerticalAnchor: 'middle',
      textAnchor: 'middle',
      refX: '105%',
      refY: '110%',
      fontSize: 12,
      fill: '#6d6d6d',
      text: '',
    },
  },
};

const transitionMarkup = {
  markup: [
    {
      tagName: 'rect',
      selector: 'body',
    },
    {
      tagName: 'text',
      selector: 'label',
    },
    {
      tagName: 'text',
      selector: 'action',
    },
  ],
};

export default {
  components: {
    EditorProperties,
    Workspace,
  },

  data() {
    return {
      loading: true,
    };
  },

  computed: {
    tool() {
      return this.$store.getters.tool;
    },
    selectedElement() {
      return this.$store.getters.selectedElement;
    },
    step() {
      return this.$store.getters.step;
    },
    actions() {
      return this.$store.getters.actions;
    },
    dataTypes() {
      return this.$store.getters.dataTypes;
    },
    graph: {
      get() { return this.$store.getters.graph; },
      set(newValue) { this.$store.commit('setGraph', newValue); },
    },
    paperEl: {
      get() { return this.$store.getters.paperEl; },
      set(newValue) { this.$store.commit('setPaperEl', newValue); },
    },
    paper: {
      get() { return this.$store.getters.paper; },
      set(newValue) { this.$store.commit('setPaper', newValue); },
    },

  },

  methods: {
    drag(x, y) {
      if (this.tool === 'cursor' && dragging) {
        const offsetX = x - dragOx;
        const offsetY = y - dragOy;
        this.paper.setOrigin(
          this.paper.options.origin.x + offsetX,
          this.paper.options.origin.y + offsetY,
        );
        window.$('body').css('cursor', 'grabbing');
      }
    },

    startDrag(x, y) {
      if (this.tool === 'cursor') {
        this.paper.setInteractivity({ elementMove: false });
        dragOx = x;
        dragOy = y;
        dragging = true;
      }
    },

    stopDrag() {
      this.paper.setInteractivity({ elementMove: true });
      dragging = false;
      window.$('body').css('cursor', 'auto');
    },

    setUpPaper() {
      window.joint.dia.Element.define('psim.Place', placeDefault, placeMarkup);
      window.joint.dia.Element.define('psim.Transition', transitionDefault, transitionMarkup);
      this.graph = new window.joint.dia.Graph();
      this.paperEl = window.$('#paper');
      this.paper = new window.joint.dia.Paper({
        el: this.paperEl,
        model: this.graph,
        width: this.paperEl.width(),
        height: this.paperEl.height(),
        gridSize: 10,
        drawGrid: true,
        linkPinning: false,
        defaultLink: () => new window.joint.shapes.standard.Link({
          router: {
            name: 'orthogonal',
            args: {
              step: 10,
              perpendicular: true,
            },
          },
        }),
        validateMagnet: (_view, magnet, evt) => magnet.getAttribute('magnet') === 'on-ctrl' && evt.ctrlKey,
        validateConnection: (viewSrc, modelSrc, viewTgt, modelTgt, end, linkView) => {
          if (viewSrc.model.attributes.type === viewTgt.model.attributes.type) return false;

          if (!viewTgt || !modelTgt) return false;

          if (modelTgt.getAttribute('port-group') === 'output') return false;

          if (viewSrc.model.attributes.type === 'psim.Place'
          && viewSrc.model.attributes.attrs.dataType.text !== modelTgt.attributes['data-type'].nodeValue) return false;

          if (viewSrc.model.attributes.type === 'psim.Transition'
          && viewTgt.model.attributes.attrs.dataType.text !== modelSrc.attributes['data-type'].nodeValue) return false;

          if (viewTgt.model.attributes.type === 'psim.Transition') {
            const portUsed = this.graph.getLinks().some((link) => (link.id !== linkView.model.id
                      && link.get('target').id === viewTgt.model.id
                      && link.get('target').port === modelTgt.getAttribute('port')));

            if (portUsed) return false;
          }

          return true;
        },
      });

      this.paper.on({
        'element:mouseenter': (cellView) => {
          if (cellView.model.attributes.type === 'psim.Place') {
            cellView.addTools(new window.joint.dia.ToolsView({
              tools: [
                new window.joint.elementTools.Remove({
                  useModelGeometry: true,
                  x: '50%',
                  y: '50%',
                  action: (evt, elementView, toolView) => {
                    this.$store.commit('removePlace', elementView.model.id);
                    elementView.model.remove({ ui: true, tool: toolView.cid });
                  },
                  offset: {
                    x: -30,
                    y: 30,
                  },
                }),
              ],
            }));
            return;
          }

          if (cellView.model.attributes.type === 'psim.Transition') {
            cellView.addTools(new window.joint.dia.ToolsView({
              tools: [
                new window.joint.elementTools.Remove({
                  useModelGeometry: true,
                  x: '0%',
                  y: '0%',
                  action: (evt, elementView, toolView) => {
                    this.$store.commit('removeTransition', elementView.model.id);
                    elementView.model.remove({ ui: true, tool: toolView.cid });
                  },
                  offset: {
                    x: 120,
                    y: -40,
                  },
                }),
              ],
            }));
          }
        },
        'link:connect': (linkView, evt, elementViewConnected, magnet) => {
          const tgtType = elementViewConnected.model.attributes.type;
          if (tgtType === 'psim.Transition') {
            this.$store.commit('addInputLink', {
              placeId: linkView.model.get('source').id,
              transitionId: linkView.model.get('target').id,
              portName: magnet.attributes['data-name'].nodeValue,
            });
          } else if (tgtType === 'psim.Place') {
            const port = this.graph.getCell(linkView.model.get('source').id).getPort(linkView.model.get('source').port);
            this.$store.commit('addOutputLink', {
              placeId: elementViewConnected.model.attributes.id,
              transitionId: linkView.model.get('source').id,
              portName: port.attrs.circle['data-name'],
            });
          }
        },
        'link:mouseenter': (linkView) => {
          linkView.addTools(new window.joint.dia.ToolsView({
            tools: [
              new window.joint.linkTools.Vertices({ snapRadius: 0 }),
              new window.joint.linkTools.Remove({
                distance: 20,
                action: (evt, _linkView, toolView) => {
                  const src = _linkView.model.get('source');
                  const tgt = _linkView.model.get('target');
                  let args = {};
                  if (src.magnet === 'body') { // src place
                    const port = this.graph.getCell(tgt.id).getPort(tgt.port);
                    args = {
                      placeId: src.id,
                      transitionId: tgt.id,
                      portName: port.attrs.circle['data-name'],
                    };
                    this.$store.commit('removeInputLink', args);
                  } else if (src.magnet === 'circle') { // src transition
                    const port = this.graph.getCell(src.id).getPort(src.port);
                    args = {
                      placeId: tgt.id,
                      transitionId: src.id,
                      portName: port.attrs.circle['data-name'],
                    };
                    this.$store.commit('removeOutputLink', args);
                  }

                  _linkView.model.remove({ ui: true, tool: toolView.cid });
                },
              }),
            ],
          }));
        },
        'cell:mouseleave': (cellView) => {
          cellView.removeTools();
        },
        'cell:pointerclick': async (cellView) => {
          if (this.tool === 'cursor') {
            if (this.selectedElement) {
              const oldCellView = this.$store.getters.getCellViewById(this.selectedElement.id);
              oldCellView.unhighlight();
            }
            const element = this.$store.getters.getElementById(cellView.model.id);
            this.$store.commit('selectElement', element);
            cellView.highlight();
            return;
          }
          if (this.tool === 'execute') {
            await this.$store.commit('fireTransition', cellView.model.attributes.id);
          }
        },
        'blank:pointerclick': (evt, x, y) => {
          if (this.tool === 'transition') {
            const cell = new window.joint.shapes.psim.Transition();
            cell.position(x, y);
            cell.addTo(this.graph);
            const element = {
              id: cell.id,
              type: 'transition',
              attrs: {
                label: 'New Transition',
                action: '',
                inputs: {},
                outputs: {},
                status: 'Enabled', // disabled, enabled, ready
              },
            };

            this.$store.commit('addElement', element);
            return;
          }

          if (this.tool === 'place') {
            const cell = new window.joint.shapes.psim.Place();
            cell.position(x, y);
            cell.addTo(this.graph);
            const element = {
              id: cell.id,
              type: 'place',
              attrs: {
                label: 'New Place',
                dataType: 'integer',
                initialState: [],
                initialStateString: '',
                currentState: [],
                currentStateString: '',
                inputs: [],
                outputs: [],
                count: 0,
              },
            };

            this.$store.commit('addElement', element);
            return;
          }
          if (this.tool === 'cursor') {
            if (this.selectedElement) {
              const oldCellView = this.$store.getters.getCellViewById(this.selectedElement.id);
              oldCellView.unhighlight();
              this.$store.commit('selectElement', undefined);
            }
          }
        },
        'blank:pointerdown': (evt, x, y) => {
          this.startDrag(x, y);
        },
        'cell:pointermove': (evt, x, y) => {
          this.drag(x, y);
        },
        'blank:pointermove': (evt, x, y) => {
          this.drag(x, y);
        },
        'cell:pointerup': () => {
          this.stopDrag();
        },
        'blank:pointerup': () => {
          this.stopDrag();
        },
        'paper:mouseleave': () => {
          this.stopDrag();
        },
      });
    },
  },

  async mounted() {
    try {
      await this.$loadScript('https://cdnjs.cloudflare.com/ajax/libs/jquery/3.5.1/jquery.js');
      await this.$loadScript('https://cdnjs.cloudflare.com/ajax/libs/lodash.js/4.17.20/lodash.js');
      await this.$loadScript('https://cdnjs.cloudflare.com/ajax/libs/backbone.js/1.4.0/backbone.js');
      await this.$loadScript('https://cdnjs.cloudflare.com/ajax/libs/jointjs/3.3.1/joint.js');

      const actionsResponse = await this.axios.get(`${psActions}/actions`);
      this.actions.push(...actionsResponse.data);

      const dataTypesResponse = await this.axios.get(`${psActions}/types`);
      this.dataTypes.push(...dataTypesResponse.data);

      this.setUpPaper();
    } catch (err) {
      // report error
      console.log(err);
      this.$alert('Failed to load 3rd party libraries', 'Error', {
        confirmButtonText: 'OK',
        type: 'error',
        callback: () => {
          this.$router.push('500');
        },
      });
    } finally {
      this.loading = false;
    }
  },
};
</script>

<style scoped>
.page-container {
  height: 95vh;
  width: 95vw;
  margin: auto;
}

.aside-container {
  background-color: rgb(238, 241, 246);
}

</style>
