<template>
  <div class="control-bar">
    <el-button-group class="tools">
      <el-tooltip
      class="item"
      effect="dark"
      content="Cursor tool"
      placement="top-start"
      :show-after="2400">
        <el-button :type="btnType.cursor" size="mini" @click="selectTool('cursor')">
          <i class="fas fa-mouse-pointer"></i>
        </el-button>
      </el-tooltip>
      <el-tooltip
      class="item"
      effect="dark"
      content="Place tool"
      placement="top-start"
      :show-after="2400">
        <el-button :type="btnType.place" size="mini" @click="selectTool('place')">
          <i class="fas fa-circle"></i>
        </el-button>
      </el-tooltip>
      <el-tooltip
      class="item"
      effect="dark"
      content="Transition tool"
      placement="top-start"
      :show-after="2400">
        <el-button :type="btnType.transition" size="mini" @click="selectTool('transition')">
          <i class="fas fa-square"></i>
        </el-button>
      </el-tooltip>
      <el-tooltip
      class="item"
      effect="dark"
      content="Execute tool"
      placement="top-start"
      :show-after="2400">
        <el-button :type="btnType.execute" size="mini" @click="selectTool('execute')">
          <i class="fas fa-bolt"></i>
        </el-button>
      </el-tooltip>
    </el-button-group>
    <el-button-group class="simulation">
      <el-tooltip
      class="item"
      effect="dark"
      content="Fast backward"
      placement="top-start"
      :show-after="2400">
        <el-button type="default" size="mini" @click="fastBackward()">
          <i class="fas fa-fast-backward"></i>
        </el-button>
      </el-tooltip>
      <el-tooltip
      class="item"
      effect="dark"
      content="Step forward"
      placement="top-start"
      :show-after="2400">
        <el-button type="default" size="mini" @click="stepForward()">
          <i class="fas fa-step-forward"></i>
        </el-button>
      </el-tooltip>
    </el-button-group>
  </div>
</template>

<script>
export default {
  name: 'ControlBar',
  data() {
    return {
      btnType: {
        cursor: 'primary',
        place: 'default',
        transition: 'default',
        execute: 'default',
      },
    };
  },

  computed: {
    tool() {
      return this.$store.getters.tool;
    },
  },

  methods: {
    selectTool(newTool) {
      this.btnType[this.tool] = 'default';
      this.$store.commit('selectTool', newTool);
      this.btnType[this.tool] = 'primary';
    },

    fastBackward() {
      this.$store.commit('resetState');
    },

    stepForward() {
      this.$store.commit('stepForward');
    },
  },
};
</script>

<style scoped>
.control-bar {
  position: absolute;
  margin: 1em 1em 1em 1em;
}

.simulation {
  margin-left: 1em;
}
</style>
