<template>
  <div class="selected-element-menu">
    <el-empty v-if="!selectedElement" description="No element selected"></el-empty>
    <el-form v-else-if="selectedElement.type === 'place'" label-width="30%" size="mini">
      <el-form-item label="Label">
        <el-input v-model="selectedElement.attrs.label" @input="updateLabel"></el-input>
      </el-form-item>
      <el-form-item label="Datatype">
        <el-select v-model="selectedElement.attrs.dataType" filterable @change="updateDataType">
          <el-option
            v-for="item in dataTypes"
            :key="item"
            :label="item"
            :value="item">
            <span class="select-item">{{ item }}</span>
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="Initial state">
        <el-input
          type="textarea"
          v-model="selectedElement.attrs.initialStateString"
          placeholder="empty"
          :autosize="{ minRows: 3, maxRows: 6 }"
          resize="none"
          @input="updateInitialStateString"
        >
        </el-input>
      </el-form-item>
      <el-form-item label="Current state">
        <el-input
          type="textarea"
          v-model="selectedElement.attrs.currentStateString"
          placeholder="empty"
          :autosize="{ minRows: 3, maxRows: 6 }"
          resize="none"
          readonly
        >
        </el-input>
      </el-form-item>
      <el-form-item label="Count">
        <el-input v-model="selectedElement.attrs.count" readonly></el-input>
      </el-form-item>
    </el-form>
    <el-form v-else-if="selectedElement.type === 'transition'" label-width="30%" size="mini">
      <el-form-item label="Label">
        <el-input v-model="selectedElement.attrs.label" @input="updateLabel"></el-input>
      </el-form-item>
      <el-form-item label="Action">
        <el-select v-model="selectedElement.attrs.action" filterable @change="updateAction">
          <el-option
            v-for="item in actions"
            :key="item.name"
            :label="item.name"
            :value="item.name">
            <span class="select-item">{{ item.name }}</span>
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="Status">
        <el-input v-model="selectedElement.attrs.status" readonly></el-input>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: 'SelectedElementMenu',

  data() {
    return {
    };
  },

  computed: {
    selectedElement() {
      return this.$store.getters.selectedElement;
    },
    actions() {
      return this.$store.getters.actions;
    },
    dataTypes() {
      return this.$store.getters.dataTypes;
    },
  },

  methods: {
    updateLabel(newValue) {
      this.$store.commit('updateElementLabel', newValue);
    },
    updateDataType(newValue) {
      this.$store.commit('updateElementDataType', newValue);
    },
    updateInitialStateString(newValue) {
      this.$store.commit('updateElementInitialStateString', newValue);
    },
    updateAction(newValue) {
      this.$store.commit('updateElementAction', newValue);
    },
  },
};
</script>
