import { createRouter, createWebHistory } from 'vue-router';
import InternalServerError from '../views/errors/InternalServerError.vue';
import Editor from '../views/Editor.vue';

const routes = [
  {
    path: '/',
    name: 'Editor',
    component: Editor,
  },
  {
    path: '/500',
    name: 'InternalServerError',
    component: InternalServerError,
  },
];

const withPrefix = (routes, prefix) => routes.map(route => ({
  ...route,
  path: `/${prefix}${route.path}`,
  name: `${prefix}-${route.name}`,
}));

console.log(routes.concat(withPrefix(routes, 'editor')))

const router = createRouter({
  history: createWebHistory(),
  routes: routes.concat(withPrefix(routes, 'editor')),
});

export default router;
