import { createStore } from 'vuex';
import axios from 'axios';
import { ElNotification } from 'element-plus';

const psActions = 'http://localhost:5000';

const successColor = '#67C23A';
const errorColor = '#F56C6C';

const transitionColor = {
  Enabled: '#8f8f8f',
  Disabled: errorColor,
  Ready: successColor,
};

const formatInitialStateView = (val) => {
  if (val.length > 8) {
    let newStr = '';
    for (let i = 0; i < val.length; i += 8) {
      if (i > 16) {
        newStr += '...';
        break;
      } else if (i + 8 < val.length) {
        newStr += `${val.slice(i, i + 8)}\n `;
      } else {
        newStr += `${val.slice(i, i + 8)}`;
      }
    }

    return newStr;
  }

  return val;
};

const validateTokens = async (data, type) => {
  const promises = [];
  data.forEach((value) => {
    const response = axios({
      method: 'POST',
      url: `${psActions}/types/check`,
      data: {
        type,
        value,
      },
      headers: {
        'Content-Type': 'application/json',
      },
    });
    promises.push(response);
  });
  try {
    const result = await Promise.all(promises);
    return result.every((response) => response.data.valid);
  } catch (err) {
    return false;
  }
};

const updateInitialState = async (tgtElement, dataString, type, state) => {
  let data;
  let validData = false;
  const element = tgtElement;
  try {
    data = JSON.parse(`[${dataString}]`);
    validData = await validateTokens(data, type);
    if (!validData) throw new Error('Error validating initial state');
  } catch (err) {
    data = [];
  } finally {
    element.attrs.initialState = data;
    const cell = state.graph.getCell(element.id);
    const initialStateFormatedString = formatInitialStateView(dataString);
    cell.attr({
      initialState: {
        fill: validData ? successColor : errorColor,
        text: `[${initialStateFormatedString}]`,
      },
    });
    if (state.step === 0) {
      const count = data ? data.length : 0;
      element.attrs.count = count;
      element.attrs.currentState = element.attrs.initialState;
      element.attrs.currentStateString = data && data.length
        ? element.attrs.initialStateString : '';
      cell.attr({
        count: {
          text: count.toString(),
        },
      });
    }
  }
};

const checkTransitionStatus = (state, transition) => {
  const inputs = Object.values(transition.attrs.inputs);
  const inputLinked = inputs.every((item) => !!item);
  if (!inputLinked) return 'Disabled';

  const reqCount = {};
  inputs.forEach((input) => {
    reqCount[input] = (reqCount[input] || 0) + 1;
  });

  const places = Object.keys(reqCount);

  const isReady = places.every((id) => {
    const place = state.elements.find((item) => id === item.id);
    return place.attrs.count >= reqCount[id];
  });

  if (isReady) return 'Ready';

  return 'Enabled';
};

const refreshTransitionStatus = (state, transitionId) => {
  const transitionElement = state.elements.find((item) => transitionId === item.id);
  transitionElement.attrs.status = checkTransitionStatus(state, transitionElement);
  const transitionCell = state.graph.getCell(transitionId);
  transitionCell.attr({
    body: {
      stroke: transitionColor[transitionElement.attrs.status],
    },
  });
};

const addLog = (state, message, type) => {
  const dt = new Date();
  const formatedDate = `${
    (dt.getMonth() + 1).toString().padStart(2, '0')}/${
    dt.getDate().toString().padStart(2, '0')}/${
    dt.getFullYear().toString().padStart(4, '0')} ${
    dt.getHours().toString().padStart(2, '0')}:${
    dt.getMinutes().toString().padStart(2, '0')}:${
    dt.getSeconds().toString().padStart(2, '0')}`;
  state.logs.push(`[${formatedDate}] ${message}`);
  if (type === 'success') {
    ElNotification({
      title: 'Success',
      message,
      type: 'success',
    });
  } else if (type === 'error') {
    ElNotification.error({
      title: 'Error',
      message,
    });
  } else if (type === 'info') {
    ElNotification.info({
      title: 'Info',
      message,
    });
  }
};

const removeLinksFromPlace = (state, placeId) => {
  const place = state.elements.find((item) => placeId === item.id);

  place.attrs.outputs.forEach((output) => {
    const transition = state.elements.find((item) => output.transitionId === item.id);
    transition.attrs.inputs[output.portName] = undefined;

    refreshTransitionStatus(state, output.transitionId);
  });

  place.attrs.inputs.forEach((input) => {
    const transition = state.elements.find((item) => input.transitionId === item.id);
    transition.attrs.outputs[input.portName] = transition.attrs.output[input.portName]
      .filter((item) => item !== input.transitionId);
  });

  state.graph.removeLinks(state.graph.getCell(placeId));
};

const fireSelectedTransition = async (state, transitionId) => {
  const transition = state.elements.find((item) => transitionId === item.id);
  const status = checkTransitionStatus(state, transition);
  const inputs = {};
  if (status === 'Ready') {
    try {
      const actionName = transition.attrs.action;
      if (!actionName) return;

      const inputPorts = Object.keys(transition.attrs.inputs);
      inputPorts.forEach((port) => {
        const place = state.elements.find((item) => item.id === transition.attrs.inputs[port]);
        inputs[port] = place.attrs.currentState.pop();
        place.attrs.count -= 1;
        const auxString = JSON.stringify(place.attrs.currentState);
        place.attrs.currentStateString = auxString.slice(1, -1);

        const cell = state.graph.getCell(place.id);
        cell.attr({
          count: {
            text: place.attrs.count.toString(),
          },
        });
      });

      const res = await axios({
        method: 'POST',
        url: `${psActions}/actions/${actionName}/execute`,
        data: inputs,
        headers: {
          'Content-Type': 'application/json',
        },
      });

      inputPorts.forEach((port) => {
        const place = state.elements.find((item) => item.id === transition.attrs.inputs[port]);

        place.attrs.outputs.forEach((transitionOutput) => {
          refreshTransitionStatus(state, transitionOutput.transitionId);
        });
      });

      const outputPorts = Object.keys(transition.attrs.outputs);

      outputPorts.forEach((port) => {
        transition.attrs.outputs[port].forEach((placeId) => {
          const place = state.elements.find((item) => item.id === placeId);
          place.attrs.count += 1;
          place.attrs.currentState.push(res.data[port]);
          const auxString = JSON.stringify(place.attrs.currentState);
          place.attrs.currentStateString = auxString.slice(1, -1);

          const cell = state.graph.getCell(place.id);
          cell.attr({
            count: {
              text: place.attrs.count.toString(),
            },
          });

          place.attrs.outputs.forEach((transitionOutput) => {
            refreshTransitionStatus(state, transitionOutput.transitionId);
          });
        });
      });

      // refreshTransitionStatus(state, transitionId);
      state.step += 1;

      const msg = `Transition "${transition.attrs.label}" fired successfully`;
      addLog(state, msg, 'success');

      return;
    } catch (err) {
      console.log(err);

      const msg = `Failed to fire transition "${transition.attrs.label}"`;
      addLog(state, msg, 'error');

      const inputsArr = Object.keys(inputs);

      inputsArr.forEach((input) => {
        const placeId = transition.attrs.inputs[input];
        const place = state.elements.find((item) => item.id === placeId);
        place.attrs.currentState.push(inputs[input]);
        place.attrs.count += 1;
        const auxString = JSON.stringify(place.attrs.currentState);
        place.attrs.currentStateString = auxString.slice(1, -1);
        const cell = state.graph.getCell(place.id);
        cell.attr({
          count: {
            text: place.attrs.count.toString(),
          },
        });
      });
    }
  }
};

export default createStore({
  state: {
    tool: 'cursor',
    selectedElement: undefined,
    elements: [],
    step: 0,
    actions: [],
    dataTypes: [],
    graph: undefined,
    paperEl: undefined,
    paper: undefined,
    logs: [],
  },
  mutations: {
    removePlace(state, placeId) {
      const place = state.elements.find((item) => item.id === placeId);
      if (state.selectedElement && place.id === state.selectedElement.id) {
        state.selectedElement = undefined;
      }

      place.attrs.outputs.forEach((output) => {
        const transition = state.elements.find((item) => item.id === output.transitionId);
        transition.attrs.inputs[output.portName] = undefined;
        refreshTransitionStatus(state, transition.id);
      });

      place.attrs.inputs.forEach((input) => {
        const transition = state.elements.find((item) => item.id === input.transitionId);
        const idxT = transition.attrs.outputs[input.portName].findIndex((item) => item === placeId);
        transition.attrs.outputs[input.portName].splice(idxT, 1);
      });

      const idx = state.elements.findIndex((item) => item.id === placeId);
      state.elements.splice(idx, 1);
    },
    removeTransition(state, transitionId) {
      const transition = state.elements.find((item) => item.id === transitionId);
      if (state.selectedElement && transition.id === state.selectedElement.id) {
        state.selectedElement = undefined;
      }

      const inputPlacesIds = Object.values(transition.attrs.inputs);

      inputPlacesIds.forEach((placeId) => {
        if (placeId) {
          const place = state.elements.find((item) => item.id === placeId);
          place.attrs.outputs = place.attrs.outputs.filter(
            (item) => item.transitionId !== transitionId,
          );
        }
      });

      const outputPorts = Object.values(transition.attrs.outputs);

      outputPorts.forEach((port) => {
        port.forEach((placeId) => {
          if (placeId) {
            const place = state.elements.find((item) => item.id === placeId);
            place.attrs.inputs = place.attrs.inputs.filter(
              (item) => item.transitionId !== transitionId,
            );
          }
        });
      });

      const idx = state.elements.findIndex((item) => item.id === transitionId);
      state.elements.splice(idx, 1);
    },
    removeInputLink(state, args) {
      const place = state.elements.find((item) => item.id === args.placeId);

      const idx = place.attrs.outputs
        .findIndex(
          (item) => item.transitionId === args.transitionId && item.portName === args.portName,
        );
      place.attrs.outputs.splice(idx, 1);

      const transition = state.elements.find((item) => item.id === args.transitionId);
      transition.attrs.inputs[args.portName] = undefined;

      refreshTransitionStatus(state, args.transitionId);
    },

    removeOutputLink(state, args) {
      const place = state.elements.find((item) => item.id === args.placeId);

      const idxP = place.attrs.inputs
        .findIndex(
          (item) => item.transitionId === args.transitionId && item.portName === args.portName,
        );
      place.attrs.inputs.splice(idxP, 1);

      const transition = state.elements.find((item) => item.id === args.transitionId);
      const idxT = transition.attrs.outputs[args.portName]
        .findIndex((item) => item === args.placeId);
      transition.attrs.outputs[args.portName].splice(idxT, 1);
    },

    stepForward: async (state) => {
      const readyTransitions = state.elements.filter((item) => item.type === 'transition' && item.attrs.status === 'Ready');
      const len = readyTransitions.length;
      if (len === 0) {
        addLog(state, 'No transition ready', 'info');
        return;
      }

      const transitionIdx = Math.floor(Math.random() * len);

      await fireSelectedTransition(state, readyTransitions[transitionIdx].id);
    },
    resetState: async (state) => {
      state.step = 0;
      const places = state.elements.filter((item) => item.type === 'place');
      const promises = [];
      places.forEach((place) => {
        promises
          .push(
            updateInitialState(place, place.attrs.initialStateString, place.attrs.dataType, state),
          );
      });

      await Promise.all(promises);

      const transitions = state.elements.filter((item) => item.type === 'transition');

      transitions.forEach((transition) => {
        refreshTransitionStatus(state, transition.id);
      });

      addLog(state, 'Model state reseted', 'info');
    },
    fireTransition: async (state, transitionId) => {
      fireSelectedTransition(state, transitionId);
    },
    addInputLink(state, args) {
      const placeElement = state.elements.find((item) => args.placeId === item.id);
      const transitionElement = state.elements.find((item) => args.transitionId === item.id);
      transitionElement.attrs.inputs[args.portName] = args.placeId;
      placeElement.attrs.outputs.push({ transitionId: args.transitionId, portName: args.portName });
      refreshTransitionStatus(state, args.transitionId);
    },
    addOutputLink(state, args) {
      const placeElement = state.elements.find((item) => args.placeId === item.id);
      const transitionElement = state.elements.find((item) => args.transitionId === item.id);

      transitionElement.attrs.outputs[args.portName].push(args.placeId);

      placeElement.attrs.inputs.push({ transitionId: args.transitionId, portName: args.portName });
    },
    selectTool(state, newTool) {
      state.tool = newTool;
    },
    addElement(state, element) {
      state.elements.push(element);
    },
    selectElement(state, element) {
      state.selectedElement = element;
    },
    setGraph(state, graph) {
      state.graph = graph;
    },
    setPaper(state, paper) {
      state.paper = paper;
    },
    setPaperEl(state, paperEl) {
      state.paperEl = paperEl;
    },
    updateElementLabel(state, label) {
      const cell = state.graph.getCell(state.selectedElement.id);
      cell.attr({
        label: {
          text: label,
        },
      });
    },
    updateElementDataType: async (state, type) => {
      const cell = state.graph.getCell(state.selectedElement.id);
      cell.attr({
        dataType: {
          text: type,
        },
      });
      await updateInitialState(
        state.selectedElement, state.selectedElement.attrs.initialStateString, type, state,
      );
      if (state.step > 0) {
        state.selectedElement.attrs.count = 0;
        cell.attr({
          count: {
            text: '0',
          },
        });
        state.selectedElement.attrs.currentStateString = '';
        state.selectedElement.attrs.currentState = [];
      }

      removeLinksFromPlace(state, state.selectedElement.id);
    },
    updateElementInitialStateString: async (state, initialStateString) => {
      await updateInitialState(
        state.selectedElement, initialStateString, state.selectedElement.attrs.dataType, state,
      );
      state.selectedElement.attrs.outputs.forEach((item) => {
        refreshTransitionStatus(state, item.transitionId);
      });
    },
    updateElementAction: (state, actionName) => {
      const action = state.actions.find((item) => item.name === actionName);

      const inputPlaces = Object.values(state.selectedElement.attrs.inputs);
      inputPlaces.forEach((placeId) => {
        if (placeId) {
          const place = state.elements.find((item) => item.id === placeId);
          place.attrs.outputs = place.attrs.outputs.filter(
            (item) => item.transitionId !== state.selectedElement.id,
          );
        }
      });

      const outputPlaces = Object.values(state.selectedElement.attrs.outputs);
      outputPlaces.forEach((placeIds) => {
        placeIds.forEach((placeId) => {
          const place = state.elements.find((item) => item.id === placeId);
          place.attrs.inputs = place.attrs.inputs.filter(
            (item) => item.transitionId !== state.selectedElement.id,
          );
        });
      });

      const cell = state.graph.getCell(state.selectedElement.id);
      state.selectedElement.attrs.action = action.name;
      state.selectedElement.attrs.inputs = {};
      state.selectedElement.attrs.outputs = {};
      cell.attr({
        action: {
          text: action.name,
        },
      });
      cell.removePorts(cell.getPorts());
      const inputs = Object.keys(action.inputs);

      inputs.forEach((input) => {
        cell.addPort({
          group: 'input',
          attrs: {
            circle: {
              'data-name': input,
              'data-type': action.inputs[input],
            },
            text: {
              text: `${input}:${action.inputs[input]}`,
            },
          },
        });
        state.selectedElement.attrs.inputs[input] = undefined;
      });

      const outputs = Object.keys(action.outputs);
      outputs.forEach((output) => {
        cell.addPort({
          group: 'output',
          attrs: {
            circle: {
              'data-name': output,
              'data-type': action.outputs[output],
            },
            text: {
              text: `${output}:${action.outputs[output]}`,
            },
          },
        });
        state.selectedElement.attrs.outputs[output] = [];
      });

      state.selectedElement.attrs.status = checkTransitionStatus(state, state.selectedElement);

      if (state.selectedElement.attrs.status === 'Disabled') {
        cell.attr({
          body: {
            stroke: errorColor,
          },
        });
      } else if (state.selectedElement.attrs.status === 'Enabled') {
        cell.attr({
          body: {
            stroke: '#8f8f8f',
          },
        });
      } else if (state.selectedElement.attrs.status === 'Ready') {
        cell.attr({
          body: {
            stroke: successColor,
          },
        });
      }
    },
  },
  getters: {
    paper(state) {
      return state.paper;
    },
    graph(state) {
      return state.graph;
    },
    paperEl(state) {
      return state.paperEl;
    },
    actions(state) {
      return state.actions;
    },
    dataTypes(state) {
      return state.dataTypes;
    },
    getElementById: (state) => (id) => state.elements.find((element) => id === element.id),
    getCellById: (state) => (id) => state.graph.getCell(id),
    getCellViewById: (state) => (id) => state.paper.findViewByModel(state.graph.getCell(id)),
    selectedElement(state) {
      return state.selectedElement;
    },
    tool(state) {
      return state.tool;
    },
    step(state) {
      return state.step;
    },
    logsString(state) {
      return state.logs.toString();
    },
  },
  actions: {
  },
  modules: {
  },
});
